// Render Prop
import React from "react";
import { object, string } from "yup";
import DftFormComponent from "../components/DftFormComponent";

const schema = object().shape({
  email: string().email()
});

const onSubmit = (
  values: any,
  { setSubmitting, setErrors /* setValues and other goodies */ }: any
) => {
  /*
    LoginToMyApp(values).then(
      user => {
        setSubmitting(false);
        // do whatevs...
        // props.updateUser(user)
      },
      errors => {
        setSubmitting(false);
        // Maybe transform your API's errors into the same shape as Formik's
        setErrors(transformMyApiErrors(errors));
      }
    );
    */
  console.log(values); // tslint:disable-line
};

const DftForm: React.SFC = () => (
  <DftFormComponent onSubmit={onSubmit} schema={schema} />
);
export default DftForm;



